export default {
  name:'NDropdowns',
  props: {
    value: {
      type: String,
      default: '',
    },
    list: {
      type: Array,
      default: [],
    },
    show: {
      type: Boolean,
      default: false,
    }
  },
  data () {
    return {
      listIndex: 0,
    }
  },
  methods: {
    clickList (item) {
      this.$emit("change", item)
    },
    handleClickOutside () {
      if (this.show) {
        this.$emit("change", '')
      }
    }
  }
}
